.home {
    height: 40rem;
    position: relative;
    overflow: hidden;

}

.home .slide {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    z-index: 1;
    display: none;
    padding: 0 1.5rem;
    animation: fade1 1s ease;
}
.home .slide.active {
    display: flex;  
}

@keyframes slide {
    0% {
        opacity: 0.8;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fade1 {
    0% {
        opacity: 0.8;
    }
    25% {
        opacity: 0.85;
    }
    75% {
        opacity: 0.9;
    }
    100% {
        opacity: 1;
    }
}


.container-slide {
    max-width: 60rem;
    margin: auto;
}

.home .container-slide {
    flex-grow: 1;
}

.home .caption {
    width: 50%;
}

.home .caption h1 {
    font-size: 4.2rem;
    color: #000000;
    margin: 0;

}

.home .slide.active .caption h1 {
    opacity: 0;
    animation: captionText .5s ease forwards;
    animation-delay: 1s;
}

.home .caption h1 {
    font-size: 4.2rem;
    color: black;
    margin: 0;
}



.home .caption p {
    font-size: 1.8rem;
    margin: 1.4rem 0 3rem;
    color: white;
}

.home .slide.active .caption p {
    opacity: 0;
    animation: captionText .5s ease forwards;
    animation-delay: 1.2s;
}

.home .caption a {
    display: inline-block;
    padding: 1rem 3rem;
    background-color: #000000;
    text-decoration: none;
    color: #ffffff;
}

.home .slide.active .caption a {
    opacity: 0;
    animation: captionText .5s ease forwards;
    animation-delay: 1.4s;
}

@keyframes captionText {
    0%{
        opacity: 0; transform: translateX(-10rem);
    }
    100%{
        opacity: 1; transform: translateX(0rem);
    }
}

.home .controls .prev, 
.home .controls .next {
    position: absolute;
    z-index: 2;
    top: 50%;
    height: 4rem;
    width: 4rem;
    margin-top: -2rem;
    color: #ffffff;
    background-color: #FF5722;
    text-align: center;
    line-height: 4rem;
    font-size: 2rem;
    cursor: pointer;
    transition: all .5s ease;
}

.home .controls .prev:hover, 
.home .controls .next:hover {
    background-color: #000000 ;
}
.home .controls .prev {
    left: 0;
}
.home .controls .next {
    right: 0;
}

.home .indicator {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 95%;
    left: 50%;
    bottom: 3rem;
    z-index: 2;
    transform: translateX(-50%);
}

@media (min-width:768px) {
    .home .indicator {
        display: flex;
        flex-direction: column;
        top: 48%;
        left: 96%;
    }
}

.home .indicator div {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    color: #ffffff;
    background-color: transparent;
    border-radius: 50%;
    border: solid 1px;
    line-height: 20rem;
    margin: .3rem .3rem;
    text-align: center;
    cursor: pointer;

    &:hover {
        background-color: aqua;
        border-color: aqua;
    }
}

.home .indicator div.active {
    background-color: aqua;
    border-color: aqua;
}

/**OCULTAR FLECHAS*/
.controls {
    display: none;
}
